
.schedule__class {
  --schedule-class-width: 100% / 3;

  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;

  position: relative;
  flex-basis: calc(var(--schedule-class-width) - 5px * 2);

  margin: 5px;
  padding: 15px;
  color: #fff;

  background: #8c8c8c;
}
.schedule__class_red {
  background: #F32830;
}
.schedule__class_blue {
  background: #3B5A6A;
}
.schedule__class_dark-blue {
  background: #243640;
}
.schedule__class_finished::after {
  content: "\f00c";
  font: 18px FontAwesome;
  display: inline-block;
  position: absolute;
  right: 15px;
  top: 15px;
  opacity: .6;
}
.schedule__class-name {
  font-size: 15px;
  font-weight: bold;
  color: #fff;
}
.schedule__class-type {
  font-weight: normal;
  display: block;
  margin-bottom: 10px;
}
.schedule__class-loc,
.schedule__info {
  list-style: none;
  margin-left: -40px;
  line-height: 26px;
  font-weight: 200;
  font-size: 13px;
  margin-bottom: 0;
}
.schedule__class-loc-dt::before,
.schedule__class-loc-place::before {
  font-size: 16px;
  opacity: .8;
  font-family: FontAwesome;
  position: relative;
  top: 1px;
}
.schedule__class-loc-dt::before {
  content: "\f017";
  padding-right: 5px;
}
.schedule__class-loc-place::before {
  content: "\f041";
  padding-right: 10px;
}
@media (max-width: 670px) {
.schedule__class {
    --schedule-class-width: 50%;
}
}
@media (max-width: 530px) {
.schedule__class {
    --schedule-class-width: 100%;
}
}



