
.teacher__name {
  margin: 50px 0 30px 0;
  text-align: center;
}
.teacher__work-place {
  font-size: 16px;
  font-weight: normal;
  margin-top: 10px;
}
.teacher__description {
  padding: 0 30%;
  line-height: 24px;
}
.teacher__description::before {
  content: "“";
  margin-left: -17px;
  position: absolute;
}
.teacher__description::after {
  content: "”";
}
.teacher__description::before,
.teacher__description::after {
  font-size: 24px;
  font-weight: bold;
  color: #ddd;
}
@media (max-width: 820px) {
.teacher__description {
    padding: 0 10%;
}
}



