
.class {
  margin-bottom: 50px;
}
.class__info {
  margin: 0 15px;
}
.class__header {
  display: flex;
  align-items: center;
  margin-top: 50px;
}
.class__return-back {
  margin-right: 20px;
}
.class__type {
  display: block;
  font-size: 14px;
  font-weight: normal;
  margin-top: 15px;
}
.class__video {
  padding: 15px;
  background: #43455d;
  height: calc(100vmin - 70px); /* site-header height */
  max-height: 1080px;
  box-sizing: border-box;
}
.class__video iframe {
  width: 100%;
  height: 100%;
  max-width: 1440px;
  margin: 0 auto;
  display: block;
}
.class__description {
  line-height: 20px;
  color: #413e56;
}
.class__teachers {
  margin-bottom: 16px;
}
.class__teachers::before {
  content: "— by ";
  color: #b5b5b5;
}
.class__schools {
  display: flex;
  flex-wrap: wrap;
}
.class__school {
  padding: 0 10px;
  color: #fff;
  border-radius: 3px;
  margin-right: 5px;
  margin-bottom: 1px;
  line-height: 30px;
}
.class__school:nth-child(n + 1) { background: #d35400;
}
.class__school:nth-child(2n + 2) { background: #2980b9;
}
.class__school:nth-child(3n + 3) { background: #2c3e50;
}
.class__map-image {
  max-width: 100%;
}
@media (max-width: 560px) {
.class__header { display: block;
}
}



